@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet() {
  @media (min-width:  640px) {
    @content;
  }
}

@mixin onNotebook() {
  @media (min-width:  1024px) {
    @content;
  }
}

@mixin onDesign() {
  @media (min-width:  1600px) {
    @content;
  }
}

@mixin onLargeScreens() {
  @media (min-width:  2560px) {
    @content;
  }
}
